import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, withRouter, useHistory } from 'react-router-dom';
import { Form, Button, Checkbox, Space, Typography, message as Message } from 'antd';
import { LOGIN_USER_REQUEST } from '../../reducers/types';
import EmailForm from './LoginForm/EmailForm';
import PasswordForm from './LoginForm/PasswordForm';

const { Title } = Typography;

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};

function LoginPage() {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const history = useHistory();
  const { loginUserLoading, loginUserDone, loginUserError, message } = useSelector(state => state.user)

  useEffect(() => {
    if (loginUserDone) {
      history.push('/')
    }
    if (loginUserError) {
      Message.error({ content: message, duration: 2 });
    }
  }, [loginUserDone, loginUserError, history, message])

  const initialValues = {
    rememberMe: true,
    email: localStorage.getItem('rememberMe'),
  }

  const onFinish = (values) => {
    if (values.rememberMe) {
      localStorage.setItem('rememberMe', values.email);
    }
    dispatch({
      type: LOGIN_USER_REQUEST,
      payload: {
        email: values.email,
        password: values.password,
      }
    })
  };

  const onFinishFailed = ({ errorFields }) => {
    form.scrollToField(errorFields[0].name);
  };


  return (
    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', height: '100vh' }}>
      <Form
        {...layout}
        name="basic"
        style={{ width: '400px' }}
        initialValues={initialValues}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Title level={2} style={{ display: 'flex', justifyContent: 'center', marginBottom: 40 }} ></Title>
        <EmailForm />
        <PasswordForm />

        <Form.Item {...tailLayout} name="rememberMe" valuePropName="checked">
          <Checkbox> </Checkbox>
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Space>
            <Button type="primary" htmlType="submit"
              loading={loginUserLoading}
              disabled={loginUserLoading}>
              
              </Button>
            <Button onClick={() => { history.goBack(1) }}>
              
            </Button>
          </Space>
        </Form.Item>

        <Form.Item  {...tailLayout} style={{ marginTop: -10 }}>
          <Link to='/register'>  </Link>
        </Form.Item>
      </Form>
    </div >
  )
}

export default withRouter(LoginPage);