import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, withRouter, useHistory } from 'react-router-dom';
import { Form, Input, Button, Typography, message as Message, Space } from 'antd';
import { UserOutlined, MailOutlined, LockOutlined, CheckSquareOutlined } from '@ant-design/icons';
import md5 from 'md5'
import { REGISTER_USER_REQUEST } from '../../reducers/types';
const { Title } = Typography;


const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
};

function RegisterPage() {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const history = useHistory();
  const { registerUserLoading, registerUserDone, registerUserError, message } = useSelector(state => state.user)

  useEffect(() => {
    if (registerUserDone) {
      history.push('/login')
    }
    if (registerUserError) {
      Message.error({ content: message, duration: 2 });
    }
  }, [registerUserDone, registerUserError, history, message])

  const onFinish = async (values) => {
    const payload = {
      email: values.email,
      name: values.userName,
      password: values.password,
      image: `http://gravatar.com/avatar/${md5(values.email)}?d=identicon`,
    }
    dispatch({
      type: REGISTER_USER_REQUEST,
      payload
    })
  };

  const onFinishFailed = ({ errorFields }) => {
    form.scrollToField(errorFields[0].name);
  };


  return (
    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', height: '100vh' }}>
      <Form
        {...layout}
        name="basic"
        style={{ width: '400px' }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Title level={2} style={{ display: 'flex', justifyContent: 'center', marginBottom: 40 }} ></Title>
        <Form.Item
          label=""
          name="email"
          rules={[{ required: true, message: ' .' },
          { type: "email", message: '   .' }]}
        >
          <Input prefix={<MailOutlined className="site-form-item-icon" />} placeholder="email" />
        </Form.Item>

        <Form.Item
          label=""
          name="userName"
          rules={[{ required: true, message: ' .' },
          { type: "string", max: 20, message: ' 20  ' }]}
        >
          <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="name" />
        </Form.Item>

        <Form.Item
          label=" "
          name="password"
          rules={[{ required: true, message: ' .' },
          { type: "string", message: '   .' },
          { whitespace: false, message: '   .' },
          { min: 6, message: ' 6 .' }]}
        >
          <Input.Password prefix={<LockOutlined className="site-form-item-icon" />} placeholder="password" />
        </Form.Item>

        <Form.Item
          label=" "
          name="passwordConfirm"
          rules={[{ required: true, message: ' .' },
          ({ getFieldValue }) => ({
            validator(rule, value) {
              if (!value || getFieldValue('password') === value) {
                return Promise.resolve();
              }
              return Promise.reject('   .');
            },
          })
          ]}
        >
          <Input.Password prefix={<CheckSquareOutlined className="site-form-item-icon" />} placeholder="password check" />
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Space >
            <Button type="primary" htmlType="submit"
              loading={registerUserLoading}
              disabled={registerUserLoading}>
              
            </Button>
            <Button onClick={() => { history.goBack(1) }} >
              
            </Button>
          </Space>
        </Form.Item>

        <Form.Item  {...tailLayout} style={{ marginTop: -10 }}>
          <Link to='/login'> </Link>
        </Form.Item>

      </Form>
    </div >
  )
}

export default withRouter(RegisterPage);