import React from 'react'
import { Descriptions, Tag, Tooltip } from 'antd'
import { useSelector } from 'react-redux'
import { v4 as uuidv4 } from 'uuid'

function MovieDescription() {

  const { currentMovie } = useSelector(state => state.movie)

  const getMovieTag = () => {
    const tags = currentMovie.genres.map(genre => {
      return <Tag key={uuidv4()}>{genre.name}</Tag>
    })
    if (currentMovie.adult) {
      tags.unshift(<Tag></Tag>)
    }
    return tags
  }

  return (
    <>
      <Descriptions key='movieDescription' size="large" column={3}>
        <Descriptions.Item key='title' span={2} label="">{currentMovie.title}</Descriptions.Item>
        <Descriptions.Item key='sort' label="">{getMovieTag()}</Descriptions.Item>
        <Descriptions.Item key='released' label="">{currentMovie.release_date}</Descriptions.Item>
        <Descriptions.Item key='runtime' label="">{`${currentMovie.runtime}`}</Descriptions.Item>
        <Descriptions.Item key='status' label="">{currentMovie.status}</Descriptions.Item>
        <Descriptions.Item key='reviewCount' label="">{currentMovie.vote_count}</Descriptions.Item>
        <Descriptions.Item key='popularity' label="">
          <Tooltip
            title={<span>     . <br />
                      , ,   ,  </span>}>
            {currentMovie.popularity}
          </Tooltip>
        </Descriptions.Item>
        <Descriptions.Item key='rate' label="">{currentMovie.vote_average}</Descriptions.Item>
        <Descriptions.Item key='description' label=""> <div style={{ width: '97%', margin: '1rem auto' }}> {currentMovie.overview}</div></Descriptions.Item>
      </Descriptions>
    </>
  )
}

export default MovieDescription
