import React from 'react';
import { useSelector } from 'react-redux';
import { Menu, Badge } from 'antd';
import styled from 'styled-components'
import StyledLink from '../../utils/StyledLink'
import StyledSubMenu from '../../utils/StyledSubMenu'

const { Item } = Menu;

const Title = styled.span`
  margin: 0 5px
`

function LeftMenu(props) {

  const { currentUser } = useSelector(state => state.user)

  return (
    <Menu mode={props.mode}>
      <Item><StyledLink to='/'>HOME</StyledLink></Item>
      <StyledSubMenu title={<Title></Title>}>
        <Item key="blog" >
          {<StyledLink to='/blog'></StyledLink>}
        </Item>
        <Item key="blog/create" disabled={!currentUser?.isAuth} >
          {<StyledLink to='/blog/create'></StyledLink>}
        </Item>
      </StyledSubMenu>

      <StyledSubMenu title={<Title></Title>}>
        <Item key="movieTrend">
          {<StyledLink to='/movieTrend'></StyledLink>}
        </Item>
        <Item key="movieTrend/favorite" disabled={!currentUser?.isAuth} >
          {<StyledLink to='/movieTrend/favorite'></StyledLink>}
        </Item>
      </StyledSubMenu>

      <StyledSubMenu title={<Title></Title>}>
        <Item key="talkative" >
          {<StyledLink to='/talkative'></StyledLink>}
        </Item>
      </StyledSubMenu>

      <StyledSubMenu popupOffset={[0, 2]}
        title={currentUser?.cart?.length
          ? <Badge dot showZero={false} offset={[0, 0]}>
            <Title></Title>
          </Badge>
          : <Title></Title>}
      >
        <Item key="jaymall">
          {<StyledLink to='/jaymall'></StyledLink>}
        </Item>
        <Item key="cart" disabled={!currentUser?.isAuth} >
          <StyledLink to='/jaymall/cart'>
            <Badge size='small' count={currentUser?.cart?.length} showZero={false} offset={[0, -10]} />
          </StyledLink>
        </Item>
        <Item key="product" disabled={!currentUser?.isAuth} >
          {<StyledLink to='/jaymall/upload'></StyledLink>}
        </Item>
      </StyledSubMenu>

      <StyledSubMenu title={<Title></Title>}>
        <Item key="jaytube" disabled>
          {<StyledLink to='/jaytube'></StyledLink>}
        </Item>
      </StyledSubMenu>

      <StyledSubMenu title={<Title></Title>}>
        <Item key="jaytweet" disabled>
          {<StyledLink to='/jaytweet'></StyledLink>}
        </Item>
      </StyledSubMenu>
    </Menu>
  )
}

export default LeftMenu
