import React from 'react'
import { Button, message as Message, Space } from 'antd'

function CartPayment() {

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <Space>
        <Button type='primary' onClick={() => { return Message.error('     .') }}></Button>
        <Button type='default' onClick={() => { return Message.error('   .') }}></Button>
        <Button type='ghost' onClick={() => { return Message.error('   .') }}></Button>
        <Button type='ghost' onClick={() => { return window.history.back() }}></Button>
      </Space>
    </div >
  )
}

export default CartPayment
